import request from '@/utils/request';
import axios from 'axios';

/**
 * 
 * @param {*} param0 { username: , password:  }
 * @returns Promise
 */
export const loginAPI = ({ username, password }) => {
  return request()({
    url: '/accounts/login',
    method: 'POST',
    data: {
      username,
      password
    }
  });
};

/**
 * 
 * @returns Promise
 */
export const logoutAPI = () => {
  return request()({
    url: '/accounts/logout',
    method: 'POST'
  });
};

/**
 * 
 * @param {*} botName 
 * @returns Promise
 */
export const getBotsDataAPI = (botName = null) => {
  const url = botName ? `/data/bots/${botName}` : '/data/bots';
  return request()({
    url: url,
    method: 'GET'
  });
};

/**
 * 
 * @param {*} botName 
 * @returns Promise
 */
export const getBotsConfigAPI = (botName = '') => {
  return request()({
    url: `/config/bots/${botName}`,
    method: 'GET'
  });
};

/**
 * 
 * @param {*} botName 
 * @param {*} config 
 * @returns Promise
 */
export const updateBotsConfigAPI = (botName, config) => {
  return request()({
    url: `/config/bots/${botName}`,
    method: 'PUT',
    data: {
      ...config
    }
  });
};

