import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import { extractConfigFromURL, submitBackendConfig } from '@/utils/backendConfig'

Vue.use(VueRouter)

// push
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err)
}

// replace
const originalReplace = VueRouter.prototype.replace
VueRouter.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch((err) => err)
}

const routes = [
  {
    path: '/',
    component: () => import('@/views/layout'),
    redirect: '/dataCenter',
    children: [
      {
        path: 'dataCenter',
        meta: { title: '' },
        component: () => import('@/views/dataCenter')
      },
      {
        path: 'bots',
        meta: { title: '' },
        component: () => import('@/views/bots')
      },
      {
        path: 'modules',
        meta: { title: '' },
        component: () => import('@/views/modules')
      },
      {
        path: 'modulesCenter',
        meta: { title: '' },
        component: () => import('@/views/modules/modulesCenter'),
        redirect: '/modulesCenter/modulesItem',
        children: [
          {
            path: 'modulesItem',
            name: 'modulesItem',
            meta: { title: '' },
            component: () => import('@/views/modules/description')
          }
        ]
      },
      {
        path: 'command',
        meta: { title: '' },
        component: () => import('@/views/command')
      },
      {
        path: 'config',
        meta: { title: '' },
        component: () => import('@/views/config')
      },
      {
        path: 'sandBox',
        meta: { title: '' },
        component: () => import('@/views/sandBox')
      },
      {
        path: 'console',
        meta: { title: '' },
        component: () => import('@/views/console')
      }
    ]
  },
  {
    path: '/login',
    meta: { title: '' },
    component: () => import('@/views/login')
  },
  {
    path: '/404',
    meta: { title: '404' },
    component: () => import('@/views/404')
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

const whiteList = ['/login', '/sandBox']

const handleQuery = (query, next) => {
  if (Object.keys(query).length > 0) {
    const configData = extractConfigFromURL(query)
    submitBackendConfig({ configData, ucb: next })
  }
  next()
}

router.beforeEach((to, from, next) => {
  const token = store.state.layoutOption.token
  if (token) {
    // 
    next()
  } else {
    // 
    if (whiteList.includes(to.path)) {
      handleQuery(to.query, next)
    } else {
      handleQuery(to.query, () => {
        next('/login')
      })
    }
  }
})

router.afterEach((to, from) => {
  const title = to.meta.title
  document.title = title ? `kams-${title}` : 'kams'
})

export default router
